import {mapState,mapMutations,mapGetters} from 'vuex';
export default {
	data() {
		return {
			isLogin:false
		};
	},
	mounted(){
		uni.getUserInfo({
			success:(detail)=>{
				console.log(detail)
				if(detail.rawData){
					this.isLogin=true;
				}
			}
		})
	},
	computed:{
		...mapState({
			cartList:(state)=>state.cart.cartList
		}),
		...mapGetters(["isSelectedAll"])
	},
	methods:{
		...mapMutations(["changeShopItemSelected","changeSelectedAllMutation"]),
		changeSelected(selected,index){
			this.changeShopItemSelected({selected,index})
			// console.log('selected',selected,index)
		},
		changeSelectedAll(selected){
			this.changeSelectedAllMutation(selected);
		}
	}
}
